import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
import { Employee, EmployeeRoot } from '../models/employee.model';

export interface EmployeeSearchParams {
  page?: number;
  pageSize?: number;
  search?: string;
}

export interface EmployeeKpiTrend {
  date: string;
  averageKpiScore: number;
  totalEmployees: number;
  highPerformers: number;
  averagePerformers: number;
  lowPerformers: number;
}

export interface EmployeeKpiTrendResponse {
  trendData: EmployeeKpiTrend[];
}

@Injectable({
  providedIn: 'root'
})
export class EmployeesService {
  private apiUrl = `${environment.apiBaseUrl}/Employees`;

  constructor(private http: HttpClient) {}

  getEmployees(params: EmployeeSearchParams = {}): Observable<EmployeeRoot> {
    let httpParams = new HttpParams();
    
    if (params.page) httpParams = httpParams.set('page', params.page.toString());
    if (params.pageSize) httpParams = httpParams.set('pageSize', params.pageSize.toString());
    if (params.search) httpParams = httpParams.set('search', params.search);
    
    return this.http.get<EmployeeRoot>(this.apiUrl, { params: httpParams });
  }

  getEmployee(id: string): Observable<Employee> {
    return this.http.get<Employee>(`${this.apiUrl}/${id}`);
  }

  addEmployee(employee: Partial<Employee>): Observable<Employee> {
    return this.http.post<Employee>(this.apiUrl, employee);
  }

  updateEmployee(id: string, employee: Partial<Employee>): Observable<Employee> {
    return this.http.put<Employee>(`${this.apiUrl}/${id}`, employee);
  }

  deleteEmployee(id: string): Observable<void> {
    return this.http.delete<void>(`${this.apiUrl}/${id}`);
  }

  getEmployeeKpiTrend(date?: string): Observable<EmployeeKpiTrendResponse> {
    let url = `${environment.apiBaseUrl}/Dashboard/employees/kpi-trend`;
    if (date) {
      url += `?date=${date}`;
    }
    return this.http.get<EmployeeKpiTrendResponse>(url);
  }
}
