import { Component, signal, OnInit, inject } from '@angular/core';
import { EmployeesService, EmployeeKpiTrend, EmployeeSearchParams } from '../../../services/employees.service';
import { Employee } from '../../../models/employee.model';
import { TranslationService } from '../../../services/translation.service';
import { Router } from '@angular/router';
import { LangService } from '../../../services/lang.service';
import { EChartsOption } from 'echarts';
import { ApexOptions } from 'ng-apexcharts';

@Component({
  selector: 'app-employees',
  standalone: false,
  templateUrl: './employees.html',
  styleUrl: './employees.css'
})
export class Employees implements OnInit {
  private employeesService = inject(EmployeesService);
  private translationService = inject(TranslationService);
  private router = inject(Router);
  private langService = inject(LangService);

  // Data
  employees: Employee[] = [];
  allEmployees: Employee[] = []; // Store all employees for filtering
  kpiTrendData: EmployeeKpiTrend[] = [];
  filteredKpiTrendData: EmployeeKpiTrend[] = [];
  totalCount = 0;
  currentPage = 1;
  pageSize = 6;
  searchTerm = '';
  
  // UI State
  loading = signal(false);
  viewMode = signal<'cards' | 'table'>('cards');
  showDeleteModalFlag = signal(false);
  showSuccessModalFlag = signal(false);
  employeeToDeleteFlag = signal<Employee | null>(null);
  successMessageFlag = signal('');
  
  // Date Filter State
  startDate = signal<string>('');
  endDate = signal<string>('');
  employeeFilterDate = signal<string>('');
  
  // Language
  currentLang = signal<'ar' | 'en'>('en');
  isRTL = signal(false);

  // ECharts Options
  totalEmployeesChartOptions: EChartsOption = {};
  echartsInitOpts = {
    renderer: 'canvas' as const,
    useDirtyRect: false
  };

  // ApexCharts Options for KPI Radial Chart
  kpiRadialChart: ApexOptions = {
    series: [0],
    chart: {
      type: 'radialBar',
      height: 320,
      width: '100%'
    },
    plotOptions: {
      radialBar: {
        startAngle: -90,
        endAngle: 90,
        track: {
          background: '#f1f1f1',
          strokeWidth: '97%',
          margin: 5
        },
        dataLabels: {
          name: {
            show: false
          },
          value: {
            offsetY: -2,
            fontSize: '28px',
            fontWeight: 'bold',
            color: '#2BA149',
            formatter: function (val: any) {
              return val + '%';
            }
          }
        }
      }
    },
    colors: ['#2BA149'],
    labels: ['KPI Score'],
    stroke: {
      lineCap: 'round'
    },
    fill: {
      type: 'solid',
      colors: ['#2BA149']
    }
  };

  // Math object for template
  Math = Math;

  ngOnInit() {
    this.initializeLanguage();
    this.loadEmployees();
    this.loadKpiTrendData();
  }

  private initializeLanguage() {
    this.langService.currentLang$.subscribe(lang => {
      this.currentLang.set(lang);
      this.isRTL.set(lang === 'ar');
    });
  }

  private loadEmployees() {
    this.loading.set(true);
    
    const params: EmployeeSearchParams = {
      page: this.currentPage,
      pageSize: this.pageSize,
      search: this.searchTerm || undefined
    };
    
    this.employeesService.getEmployees(params).subscribe({
      next: (res) => {
        this.employees = res.data.items;
        this.allEmployees = [...res.data.items]; // Store all employees
        this.totalCount = res.data.totalCount;
        this.currentPage = res.data.page;
        this.pageSize = res.data.pageSize;

        this.loading.set(false);
      },
      error: (error) => {
        console.error('Error loading employees:', error);
        this.loading.set(false);
      }
    });
  }

  private loadKpiTrendData(date?: string) {
    this.employeesService.getEmployeeKpiTrend(date).subscribe({
      next: (res) => {
        this.kpiTrendData = res.trendData;
        this.filteredKpiTrendData = [...this.kpiTrendData];
        this.initializeDateRange();
        // Initialize charts after data is loaded
        setTimeout(() => {
          this.initializeCharts();
        }, 200);
      },
      error: (error) => {
        console.error('Error loading KPI trend data:', error);
      }
    });
  }

  private initializeDateRange() {
    if (this.kpiTrendData.length > 0) {
      const dates = this.kpiTrendData.map(d => new Date(d.date)).sort((a, b) => a.getTime() - b.getTime());
      this.startDate.set(dates[0].toISOString().split('T')[0]);
      this.endDate.set(dates[dates.length - 1].toISOString().split('T')[0]);
    }
  }

  maxDate(): string {
    return new Date().toISOString().split('T')[0];
  }

  onStartDateChange(event: Event) {
    const target = event.target as HTMLInputElement;
    this.startDate.set(target.value);
  }

  onEndDateChange(event: Event) {
    const target = event.target as HTMLInputElement;
    this.endDate.set(target.value);
  }

  applyDateFilter() {
    if (!this.startDate() || !this.endDate()) {
      this.filteredKpiTrendData = [...this.kpiTrendData];
      return;
    }

    const start = new Date(this.startDate());
    const end = new Date(this.endDate());
    
    this.filteredKpiTrendData = this.kpiTrendData.filter(data => {
      const dataDate = new Date(data.date);
      return dataDate >= start && dataDate <= end;
    });
  }

  resetDateFilter() {
    this.filteredKpiTrendData = [...this.kpiTrendData];
    this.initializeDateRange();
  }

  // Employee Date Filter Methods
  onEmployeeDateChange(event: Event) {
    const target = event.target as HTMLInputElement;
    this.employeeFilterDate.set(target.value);
  }

  applyEmployeeFilter() {
    if (!this.employeeFilterDate()) {
      this.employees = [...this.allEmployees];
      return;
    }

    const filterDate = new Date(this.employeeFilterDate());
    filterDate.setHours(0, 0, 0, 0); // Set to start of day
    
    this.employees = this.allEmployees.filter(employee => {
      const hireDate = new Date(employee.hireDate);
      hireDate.setHours(0, 0, 0, 0); // Set to start of day
      return hireDate.getTime() === filterDate.getTime();
    });

    this.totalCount = this.employees.length;
    this.currentPage = 1; // Reset to first page
  }

  resetEmployeeFilter() {
    this.employees = [...this.allEmployees];
    this.employeeFilterDate.set('');
    this.totalCount = this.allEmployees.length;
    this.currentPage = 1;
  }

  // Unified Filter Methods
  applyUnifiedFilter() {
    const selectedDate = this.employeeFilterDate();
    
    if (!selectedDate) {
      // Reset both employee and KPI data to show all
      this.resetUnifiedFilter();
      return;
    }

    // Filter employees by hire date
    this.applyEmployeeFilter();
    
    // Load KPI data for the selected date
    this.loadKpiTrendData(selectedDate);
  }

  resetUnifiedFilter() {
    // Reset employee filter
    this.employees = [...this.allEmployees];
    this.employeeFilterDate.set('');
    this.totalCount = this.allEmployees.length;
    this.currentPage = 1;
    
    // Reset KPI data to show all
    this.loadKpiTrendData();
  }

  changePage(page: number) {
    if (page >= 1 && page <= this.totalPages) {
      this.currentPage = page;
      this.loadEmployees();
    }
  }

  onSearchChange(searchTerm: string) {
    this.searchTerm = searchTerm;
    this.currentPage = 1;
    this.loadEmployees();
  }

  onSearchClick() {
    this.currentPage = 1;
    this.loadEmployees();
  }

  clearSearch() {
    this.searchTerm = '';
    this.currentPage = 1;
    this.loadEmployees();
  }

  onPageSizeChange(newPageSize: number) {
    this.pageSize = newPageSize;
    this.currentPage = 1;
    this.loadEmployees();
  }

  get totalPages(): number {
    return Math.ceil(this.totalCount / this.pageSize);
  }

  get pages(): number[] {
    const pages: number[] = [];
    const maxVisiblePages = 5;
    
    if (this.totalPages === 0) {
      pages.push(1);
      return pages;
    }
    
    let startPage = Math.max(1, this.currentPage - Math.floor(maxVisiblePages / 2));
    let endPage = Math.min(this.totalPages, startPage + maxVisiblePages - 1);
    
    if (endPage - startPage + 1 < maxVisiblePages) {
      startPage = Math.max(1, endPage - maxVisiblePages + 1);
    }
    
    for (let i = startPage; i <= endPage; i++) {
      pages.push(i);
    }
    
    return pages;
  }

  getFullName(employee: Employee): string {
    return `${employee.firstName} ${employee.lastName}`;
  }

  getInitials(employee: Employee): string {
    return `${employee.firstName.charAt(0)}${employee.lastName.charAt(0)}`.toUpperCase();
  }

  getStatusClass(isActive: boolean): string {
    return isActive ? 'status-active' : 'status-inactive';
  }

  copyToClipboard(text: string) {
    navigator.clipboard.writeText(text).then(() => {
      // Could show a toast notification here
      console.log('Copied to clipboard:', text);
    });
  }

  goToEdit(id: string) {
    this.router.navigate(['/employees/edit', id]);
  }

  // KPI Trend Helper Methods
  getLatestKpiScore(): number {
    if (this.filteredKpiTrendData.length === 0) return 0;
    return Math.round(this.filteredKpiTrendData[this.filteredKpiTrendData.length - 1].averageKpiScore);
  }

  getLatestTotalEmployees(): number {
    if (this.filteredKpiTrendData.length === 0) return 0;
    return this.filteredKpiTrendData[this.filteredKpiTrendData.length - 1].totalEmployees;
  }

  getLatestHighPerformers(): number {
    if (this.filteredKpiTrendData.length === 0) return 0;
    return this.filteredKpiTrendData[this.filteredKpiTrendData.length - 1].highPerformers;
  }

  getLatestLowPerformers(): number {
    if (this.filteredKpiTrendData.length === 0) return 0;
    return this.filteredKpiTrendData[this.filteredKpiTrendData.length - 1].lowPerformers;
  }

  getHighPerformersPercentage(): number {
    if (this.kpiTrendData.length === 0) return 0;
    const latest = this.kpiTrendData[this.kpiTrendData.length - 1];
    const total = latest.totalEmployees;
    return total > 0 ? (latest.highPerformers / total) * 100 : 0;
  }

  getLowPerformersPercentage(): number {
    if (this.kpiTrendData.length === 0) return 0;
    const latest = this.kpiTrendData[this.kpiTrendData.length - 1];
    const total = latest.totalEmployees;
    return total > 0 ? (latest.lowPerformers / total) * 100 : 0;
  }

  getKpiScorePath(): string {
    if (this.filteredKpiTrendData.length < 2) return '';
    
    const width = 300;
    const height = 60;
    const step = width / (this.filteredKpiTrendData.length - 1);
    
    let path = `M 0 ${height - (this.filteredKpiTrendData[0].averageKpiScore / 100) * height}`;
    
    for (let i = 1; i < this.filteredKpiTrendData.length; i++) {
      const x = i * step;
      const y = height - (this.filteredKpiTrendData[i].averageKpiScore / 100) * height;
      path += ` L ${x} ${y}`;
    }
    
    return path;
  }

  getEmployeesPath(): string {
    if (this.filteredKpiTrendData.length < 2) return '';
    
    const width = 300;
    const height = 60;
    const maxEmployees = Math.max(...this.filteredKpiTrendData.map(d => d.totalEmployees));
    const step = width / (this.filteredKpiTrendData.length - 1);
    
    let path = `M 0 ${height - (this.filteredKpiTrendData[0].totalEmployees / maxEmployees) * height}`;
    
    for (let i = 1; i < this.filteredKpiTrendData.length; i++) {
      const x = i * step;
      const y = height - (this.filteredKpiTrendData[i].totalEmployees / maxEmployees) * height;
      path += ` L ${x} ${y}`;
    }
    
    return path;
  }

  // ECharts Initialization Methods
  initializeTotalEmployeesChart(): void {
    const totalEmployees = this.getLatestTotalEmployees();
    
    // Ensure we have valid data
    if (totalEmployees === null || totalEmployees === undefined || isNaN(totalEmployees)) {
      this.totalEmployeesChartOptions = {};
      return;
    }
    
    // Single color for all elements
    const chartColor = '#34495E'; // Tech Blue 600
    
    this.totalEmployeesChartOptions = {
      grid: {
        top: '20%',
        left: '5%',
        right: '5%',
      },
      series: [
        {
          type: 'gauge',
          startAngle: 200,
          endAngle: -20,
          min: 0,
          max: Math.max(totalEmployees, 100),
          splitNumber: 10,
          radius: '80%',
          axisLine: {
            lineStyle: {
              width: 6,
              color: [[1, chartColor]]
            }
          },
          progress: {
            show: true,
            width: 6,
            itemStyle: {
              color: chartColor
            }
          },
          pointer: {
            show: true,
            length: '80%',
            width: 3,
            itemStyle: {
              color: chartColor
            }
          },
          axisTick: {
            show: true,
            distance: -18,
            length: 8,
            lineStyle: {
              color: '#999',
              width: 1
            }
          },
          splitLine: {
            show: true,
            distance: -30,
            length: 15,
            lineStyle: {
              color: '#34495E',
              width: 1
            }
          },
          axisLabel: {
            show: true,
            distance:-5,
            color: '#666',
            fontSize: 10
          },
          detail: {
            show: true,
            fontSize: 40,
            offsetCenter: [0, '60%'],
            formatter: '{value}',
            color: chartColor,
            fontWeight: 'bold'
          },
          data: [
            {
              value: totalEmployees
            }
          ]
        }
      ]
    };
  }

  // Initialize KPI Radial Chart using ApexCharts
  initializeKpiRadialChart(): void {
    const kpiScore = this.getLatestKpiScore();
    
    // Update only the series value
    this.kpiRadialChart.series = [kpiScore];
  }

  // Method to initialize all charts
  initializeCharts(): void {
    try {
      console.log('Initializing charts...');
      this.initializeTotalEmployeesChart();
      this.initializeKpiRadialChart();
      console.log('Charts initialized successfully');
    } catch (error) {
      console.error('Error initializing charts:', error);
    }
  }

  goAddEmployee() {
    this.router.navigate(['/employees/add']);
  }

  openDeleteModal(employee: Employee) {
    this.employeeToDeleteFlag.set(employee);
    this.showDeleteModalFlag.set(true);
  }

  closeDeleteModal() {
    this.showDeleteModalFlag.set(false);
    this.employeeToDeleteFlag.set(null);
  }

  confirmDelete() {
    const employee = this.employeeToDeleteFlag();
    console.log('Confirm delete called, employee:', employee);
    if (employee) {
      console.log('Proceeding with delete for employee ID:', employee.id);
      this.deleteEmployee(employee.id);
    } else {
      console.error('No employee selected for deletion');
    }
  }

  private deleteEmployee(id: string) {
    console.log('Attempting to delete employee with ID:', id);
    this.loading.set(true);
    
    this.employeesService.deleteEmployee(id).subscribe({
      next: (response) => {
        console.log('Delete API response:', response);
        this.loading.set(false);
        this.displaySuccessModal('employees.deleted_success');
        this.closeDeleteModal();
        this.loadEmployees(); // Reload the list
      },
      error: (error) => {
        console.error('Error deleting employee:', error);
        this.loading.set(false);
        
        // Show error message to user
        if (error.status === 404) {
          this.displaySuccessModal('employees.delete_error_not_found');
        } else if (error.status === 403) {
          this.displaySuccessModal('employees.delete_error_permission');
        } else {
          this.displaySuccessModal('employees.delete_error_general');
        }
      }
    });
  }

  private displaySuccessModal(messageKey: string) {
    this.successMessageFlag.set(this.translate(messageKey));
    this.showSuccessModalFlag.set(true);
    this.loading.set(false);
  }

  closeSuccessModal() {
    this.showSuccessModalFlag.set(false);
    this.successMessageFlag.set('');
  }

  // Getters for template
  showDeleteModal() {
    return this.showDeleteModalFlag();
  }

  showSuccessModal() {
    return this.showSuccessModalFlag();
  }

  employeeToDelete() {
    return this.employeeToDeleteFlag();
  }

  successMessage() {
    return this.successMessageFlag();
  }

  getDeleteMessage(): string {
    const employee = this.employeeToDelete();
    if (employee) {
      const name = this.getFullName(employee);
      return this.translate('employees.confirm_delete_message').replace('%name%', name);
    }
    return this.translate('employees.confirm_delete');
  }



  // Translation helper
  translate(key: string): string {
    return this.translationService.translate(key);
  }
}
